import { Component } from '@angular/core'
import { TranslateService } from '@ngx-translate/core'
import { environment } from 'src/environments/environment'
import { ActivityComponent } from '../activities/activity.component'
import { IUser } from '../api-interfaces'
import { SessionService } from '../services/session.service'

@Component({
    selector: 'user-activity',
    templateUrl: 'user-activity.component.html',
})
export class UserActivityComponent extends ActivityComponent {
    public displayName = environment.displayName
    constructor(translate: TranslateService, private session: SessionService) {
        super(translate)
    }

    public isSessionUser(user: IUser | null): boolean {
        if (!this.session.user) {
            return false
        }
        return !!user && this.session.user.id === user.id
    }
}
