import { HttpClient, HttpResponse } from '@angular/common/http'
import { ChangeDetectorRef, Component } from '@angular/core'
import { DomSanitizer } from '@angular/platform-browser'
import { ActivatedRoute, Router } from '@angular/router'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { Observable } from 'rxjs'
import { flatMap, take, tap } from 'rxjs/operators'
import { UAParser } from 'ua-parser-js'
import { ParsedActivity } from '../common/activities/activities.component'
import { ListComponent } from '../common/list.component'
import { ConfirmationService } from '../common/services/confirmation.service'
import { SessionService } from '../common/services/session.service'

@Component({
    selector: 'user-activities',
    templateUrl: 'user-activities.component.html',
})
export class UserActivitiesComponent extends ListComponent<ParsedActivity> {
    public min = Math.min
    public limit = 10

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        private sanitizer: DomSanitizer
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
    }

    public find(): Observable<HttpResponse<any[]>> {
        return this.session.userStream.pipe(
            take(1),
            tap(user => {
                this.apiUrl = `/users/${user.id}/activities`
            }),
            flatMap(() => super.find())
        )
    }

    protected mapItem(item: any): ParsedActivity {
        item.parsedUserAgent = new UAParser(item.userAgent).getResult()
        item.dataHtml = this.sanitizer.bypassSecurityTrustHtml(item.dataHtml)
        return item
    }
}
